import FeedPostClient from "@/components/feed/FeedPostClient";
// üí° [ÏàòÏ†ï] Ìó¨Ìçº ÌååÏùºÏóêÏÑú import
import {
  getGeminiProductFeed,
  FeedItem,
} from "@/utils/helper"; // üëà Í≤ΩÎ°ú ÏàòÏ†ï
import { redirect } from "next/navigation";

export default async function FeedItemPage({
  params,
}: {
  params: Promise<{ id: string }>; // üí° 1. await params ÌÉÄÏûÖ (ÏàòÏ†ï ÏôÑÎ£åÎê®)
}) {
  const { id: safeGuid } = await params; // üí° 2. await ÏÇ¨Ïö© (ÏàòÏ†ï ÏôÑÎ£åÎê®)
  let item: FeedItem | undefined;

  if (!safeGuid) {
    redirect("/");
  }

  try {
    // üí° 3. [ÌïµÏã¨ ÏàòÏ†ï]
    // Ïã§Ìå®Ìïú atob, replace, padding Î°úÏßÅÏùÑ Î™®Îëê Ï†úÍ±∞ÌïòÍ≥†
    // Node.jsÏùò BufferÎ°ú Ìïú Î≤àÏóê ÎîîÏΩîÎî©Ìï©ÎãàÎã§.
    // 'base64url' Ïù∏ÏΩîÎî©ÏùÄ +, / ÎåÄÏã† -, _Î•º ÏÇ¨Ïö©ÌïòÍ≥† Ìå®Îî©(=)ÏùÑ ÏûêÎèôÏúºÎ°ú Ï≤òÎ¶¨Ìï©ÎãàÎã§.
    const guid = Buffer.from(safeGuid, "base64url").toString("utf8");

    const items = await getGeminiProductFeed();
    item = items.find((i: FeedItem) => i.guid === guid);

  } catch (error) {
    // Buffer ÎîîÏΩîÎî© Ïã§Ìå® ÎòêÎäî getGeminiProductFeed Ïã§Ìå® Ïãú
    console.error("Failed to decode GUID or find item:", error);
    redirect("/");
  }

  if (!item) {
    redirect("/");
  }

  return <FeedPostClient item={item} />;
}